.main-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 95vh; }

.main-container-child {
  display: flex;
  flex-flow: column;
  min-width: 250px;
  max-width: 500px;
  width: 100%;
  height: 100vh;
  max-height: 600px;
  min-height: 500px;
  align-self: center;
  align-items: center;
  justify-content: space-around;
  background-color: #eee; }

.signinbtn {
  background-color: darkcyan;
  border: none;
  outline: none;
  color: white;
  width: 80%;
  height: 35px;
  border-radius: 5px;
  transition: all linear 0.4s; }

.signinbtn:hover {
  background-color: white;
  color: darkcyan;
  border: 1px solid darkcyan;
  transition: all linear 0.4s; }

.signin-input {
  width: 80%;
  border: none;
  outline: none;
  border-bottom: 1px solid darkcyan;
  background-color: #eee; }

.apple-login-btn {
  background-color: black;
  color: white;
  border: none;
  outline: none;
  color: white;
  width: 80%;
  height: 35px;
  border-radius: 5px;
  transition: all linear 0.3s; }

.google-login-btn {
  background-color: gray;
  color: white;
  border: none;
  outline: none;
  color: white;
  width: 80%;
  height: 35px;
  border-radius: 5px;
  transition: all linear 0.3s; }

.apple-login-btn:hover {
  background-color: white;
  border: 1px solid black;
  color: black;
  transition: all linear 0.3s; }

.google-login-btn:hover {
  background-color: white;
  border: 1px solid gray;
  color: gray;
  transition: all linear 0.3s; }
