.signinbtn {
  background-color: darkcyan;
  border: none;
  outline: none;
  color: white;
  width: 80%;
  height: 35px;
  border-radius: 5px;
  transition: all linear 0.4s; }

.signinbtn:hover {
  background-color: white;
  color: darkcyan;
  border: 1px solid darkcyan;
  transition: all linear 0.4s; }

.signin-input {
  width: 80%;
  border: none;
  outline: none;
  border-bottom: 1px solid darkcyan;
  margin-top: 10px;
  background-color: #eee; }

.apple-login-btn {
  background-color: black;
  color: white;
  border: none;
  outline: none;
  color: white;
  width: 80%;
  height: 35px;
  border-radius: 5px;
  transition: all linear 0.3s; }

.google-login-btn {
  background-color: gray;
  color: white;
  border: none;
  outline: none;
  color: white;
  width: 80%;
  height: 35px;
  border-radius: 5px;
  transition: all linear 0.3s; }

.apple-login-btn:hover {
  background-color: white;
  border: 1px solid black;
  color: black;
  transition: all linear 0.3s; }

.google-login-btn:hover {
  background-color: white;
  border: 1px solid gray;
  color: gray;
  transition: all linear 0.3s; }
